





















import { Component, Mixins } from 'vue-property-decorator'
import UploadFile from './UploadFile.vue'
import 'jodit/build/jodit.min.css'
import PopupMixin from '@/mixins/popup'
import AppointCustomer from '@/modules/customer/components/popup/AppointCustomer.vue'
import PopupAddUploadFile from '@/modules/customer/components/popup/PopupAddUploadFile.vue'
import { API_URL } from '@/configs'

// @ts-ignore
import { JoditEditor } from 'jodit-vue'
@Component({ components: { JoditEditor, UploadFile, AppointCustomer, PopupAddUploadFile } })
export default class Header extends Mixins(PopupMixin) {
  content = '<p><br></p>'
  buttons = ['bold', 'italic', 'underline', 'link', 'ul', 'ol', 'image']

  config = {
    spellcheck: false,
    showCharsCounter: false,
    showWordsCounter: false,
    autofocus: true,
    showXPathInStatusbar: false,
    language: 'vi',
    minHeight: 120,
    i18n: {
      vi: {
        'Type something': 'Nhp ghi ch...',
        'Search for': 'Nhp tm kim'
      }
    },
    uploader: {
      url: `${API_URL}/file/v1/district/${this.$store.state.beUser.districtId}/upload`,
      format: 'json',
      headers: {
        Authorization: `Bearer ${this.$store.state.beAuth.access_token}`
      },
      isSuccess: function (resp: Record<string, any>): any {
        return !resp.error
      },
      getMessage: function (resp: Record<string, any>): string {
        return resp.msg
      },
      filesVariableName: function (e: any): string {
        return 'files'
      },
      process: function (resp: Record<string, any>): Record<string, any> {
        return {
          files: resp.data || []
        }
      },
      defaultHandlerSuccess: function (data: Record<string, any>): void {
        const field = 'files'
        console.log('res', data)

        if (data[field] && data[field]['success'].length) {
          for (let i = 0; i < data[field]['success'].length; i += 1) {
            console.log('hi', data[field]['success'][i].url)

            //@ts-ignore
            this.s.insertImage(data[field]['success'][i].url)
          }
        }
      },
      error: function (e: any): void {
        console.error(e)
      }
    }
  }
  active = 'note'
  tabs: Array<Record<string, any>> = [
    {
      id: 1,
      title: 'Ghi ch',
      icon: 'note',
      value: 'note'
    },
    {
      id: 2,
      title: 'Lch hn',
      icon: 'calendar',
      value: 'calendar'
    },
    {
      id: 3,
      title: 'File nh km',
      icon: 'file',
      value: 'file'
    }
  ]

  handleChangeTab(tab: string): void {
    // this.active = tab
    if (tab === 'calendar') {
      this.$emit('addCalendar', { popupName: 'appoint-customer', type: 'create' })
    }
    if (tab === 'file') {
      this.$emit('addCalendar', { popupName: 'add-upload-file', type: 'create' })
    }
  }
  cancelNote(): void {
    console.log(this.$store.state)

    this.content = '<p><br></p>'
  }

  handleCreateNote(): void {
    this.$emit('create', this.content)
  }
}
