
















































import { Component, Mixins } from 'vue-property-decorator'
import HeaderNewsFeed from '@/components/newsfeed/Header.vue'
import DistrictMixin from '@/mixins/commonMixin'
import PopupMixin from '@/mixins/popup'
import getRepository from '@/services'
import DealRepository from '@/services/repositories/deal'
import { namespace } from 'vuex-class'
import { debounce, includes } from 'lodash'
import PopupNote from '../../components/popup/PopupNote.vue'
import CreateDeal from '@/modules/deals/components/popup/CreateDeal.vue'
import PopupApointCustomer from '../../components/popup/AppointCustomer.vue'
import EventBus from '@/utils/eventBus'
import PopupAddUploadFile from '../../components/popup/PopupAddUploadFile.vue'
import PopupConfirmType from '../../components/popup/PopupConfirmType.vue'
const beCustomer = namespace('beCustomer')
import PostItem from '../../../../components/newsfeed/PostItem.vue'
import PopupDeleteAll from '../../components/popup/PopupDeleteAll.vue'

@Component({ components: { PostItem, HeaderNewsFeed, PopupNote, CreateDeal, PopupApointCustomer, PopupAddUploadFile, PopupConfirmType, PopupDeleteAll } })
export default class CustomerDetail extends Mixins(DistrictMixin, PopupMixin) {
  @beCustomer.State('detailCustomer') detailCustomer!: Record<string, any>

  apiCustomer!: DealRepository

  dataUpcomming: Array<Record<string, any>> = []
  dataPast: Array<Record<string, any>> = []
  active = ''
  tabs: Array<Record<string, any>> = [
    {
      id: 0,
      title: 'Tt c',
      value: ''
    },
    {
      id: 1,
      title: 'Ghi ch',
      value: 'NOTE'
    },
    {
      id: 2,
      title: 'Lch hn',
      value: 'CALENDAR'
    },
    {
      id: 3,
      title: 'File nh km',
      value: 'FILE'
    },
    {
      id: 4,
      title: 'C hi bn hng',
      value: 'DEAL'
    }
  ]

  post: Record<string, any> = {}
  type = 'edit'
  customerId: any = 0
  idUploadDeal: any = 0
  message = ''

  propDeal: Record<string, any> = {}
  dataCustomer: any = {
    id: '',
    customerFullName: '',
    customerCifCode: ''
  }
  propCalendar: Record<string, any> = {}

  isLoadingPost = false
  isEndPost = false

  query: Record<string, any> = {
    page: 1,
    limit: 20,
    timeType: 'PAST',
    newsfeedType: ''
  }
  dataDetail: any = {}
  dataDetailDeal: any = {}

  get tabRouter(): Array<Record<string, any>> {
    if (this.$route.path.includes('khach-hang')) {
      return this.tabs
    }
    return [
      {
        id: 0,
        title: 'Tt c',
        value: ''
      },
      {
        id: 1,
        title: 'Ghi ch',
        value: 'NOTE'
      },
      {
        id: 2,
        title: 'Lch hn',
        value: 'CALENDAR'
      },
      {
        id: 3,
        title: 'File nh km',
        value: 'FILE'
      }
    ]
  }
  created(): void {
    this.apiCustomer = getRepository('deal', {
      districtId: this.districtId
    })
    // this.customerId = this.$route.params.id
    // this.dataDetail = this.detailCustomer
    // console.log('khach hang',this.detailCustomer)

    if (includes(this.$route.path, 'khach-hang')) {
      this.customerId = this.$route.params.id
      this.dataDetail = this.detailCustomer
      console.log('khach hang', this.detailCustomer)
    } else {
      this.customerId = this.detailCustomer.customerId
      this.idUploadDeal = this.detailCustomer.id
      this.dataDetailDeal = this.detailCustomer
      console.log('deal', this.detailCustomer)
    }
    // EventBus.$on('editDetailHeader', (data: any) => {
    //   console.log('data: ', data)
    //   this.propDeal.title = data.title
    //   this.propDeal.code = data.code
    //   this.propDeal.id = data.id
    //   this.setOpenPopup({
    //     popupName: 'create-deal',
    //     isOpen: true
    //   })
    // })
    // EventBus.$on('createAddDealCustomer', (dataPopup: any, dataCustomer: any) => {
    //   this.propDeal.title = dataPopup.title
    //   this.propDeal.code = dataPopup.code
    //   this.dataCustomer = dataCustomer
    //   this.setOpenPopup({
    //     popupName: 'create-deal',
    //     isOpen: true
    //   })
    // })

    EventBus.$on('reloadDeal', () => {
      this.init()
    })
    EventBus.$on('editPost', (post: Record<string, any>) => {
      this.handleUpdatePost(post)
    })
    EventBus.$on('reloadNewsfeed', () => {
      this.init()
    })
  }

  mounted(): void {
    this.init()
    const elm: any = document.querySelector('.el-main.customer')
    setTimeout(() => {
      elm.addEventListener('scroll', this.handleScroll)
    }, 1000)
  }

  destroyed(): void {
    EventBus.$off('reloadDeal')
    EventBus.$off('editPost')
  }

  handleScroll(): void {
    const elm: any = document.querySelector('.el-main.customer')
    if (elm.scrollTop >= elm.scrollHeight - elm.offsetHeight && !this.isEndPost) {
      this.query.page += 1
      this.debounceLoad()
    }
  }

  debounceLoad = debounce(() => {
    this.getListPast(true)
    // this.getSummary()
  }, 500)

  init(): void {
    this.getListUpcomming()
    this.getListPast()
  }

  async getListPast(isAdd = false): Promise<void> {
    const type = includes(this.$route.path, 'khach-hang') ? 'CUSTOMER' : 'DEAL'
    const key = type === 'CUSTOMER' ? 'customerId' : 'dealId'
    try {
      const params = {
        ...this.query,
        [`${key}`]: type === 'CUSTOMER' ? this.customerId + '' : this.$route.params.id
      }
      const result = await this.apiCustomer.getNewsFeed(params)
      this.isEndPost = result.content && result.content.length ? false : true
      if (isAdd) {
        this.dataPast = [...this.dataPast, ...result.content]
      } else {
        this.dataPast = result.content
      }
    } catch (error) {
      console.log(error)
    }
  }
  async getListUpcomming(): Promise<void> {
    const type = includes(this.$route.path, 'khach-hang') ? 'CUSTOMER' : 'DEAL'
    const key = type === 'CUSTOMER' ? 'customerId' : 'dealId'
    try {
      const params = {
        timeType: 'UPCOMMING',
        newsfeedType: 'CALENDAR',
        [`${key}`]: type === 'CUSTOMER' ? this.customerId + '' : this.$route.params.id
      }
      const result = await this.apiCustomer.getNewsFeed(params)
      this.dataUpcomming = result.content
    } catch (error) {
      console.log(error)
    }
  }

  resetQuery(): void {
    this.query.page = 1
    this.query.limit = 20
  }

  async handleChangeTab(tab: string): Promise<void> {
    this.isLoadingPost = true
    this.isEndPost = false
    this.active = tab
    this.query.page = 1
    this.query.limit = 20
    this.query.newsfeedType = tab
    await this.getListPast()
    this.isLoadingPost = false
  }

  handleCreateNote(content: string): void {
    const type = includes(this.$route.path, 'khach-hang') ? 'CUSTOMER' : 'DEAL'
    const key = type === 'CUSTOMER' ? 'customerId' : 'dealId'
    const data = {
      description: content,
      [`${key}`]: type === 'CUSTOMER' ? this.customerId + '' : this.$route.params.id
    }
    this.apiCustomer.createNote(type, data).then(() => {
      this.$message.success('To ghi ch thnh cng')
      //@ts-ignore
      this.$refs.headerNewsFeed?.cancelNote()
      this.resetQuery()
      this.init()
    })
  }

  checkTypePost(type: string): string {
    switch (type) {
      case 'NOTE':
        return 'ghi ch'

      case 'CALENDAR':
        return 'lch hn'

      case 'DEAL':
        return 'c hi bn hng'

      default:
        return 'file ti liu'
    }
  }

  handleUpdatePost(post: Record<string, any>): void {
    this.apiCustomer.updatePost(post.id, post).then(() => {
      const text = this.checkTypePost(post.newsfeedType)
      this.$message.success(`Sa ${text} thnh cng`)
      this.resetQuery()
      this.init()
    })
  }

  handleLoad(): void {
    this.resetQuery()
    this.init()
  }
  loadData(): void {
    this.resetQuery()
    this.init()
  }

  handleEdit(post: Record<string, any>): void {
    this.post = { ...post }
    if (post.newsfeedType === 'NOTE') {
      // this.type === 'edit'
      // this.setOpenPopup({
      //   popupName: 'popup-note',
      //   isOpen: true
      // })
      this.handleOpenPopup({ popupName: 'popup-note', type: 'edit' })
    }
    if (post.newsfeedType === 'DEAL') {
      // this.propDeal.title = 'Sa thng tin c hi bn hng'
      // this.propDeal.code = 'edit'
      // this.propDeal.id = post.objectId
      // this.setOpenPopup({
      //   popupName: 'create-deal',
      //   isOpen: true
      // })

      // emit sang component detailHeader  m popup
      EventBus.$emit('openEditDeal', this.post)
    }
    if (post.newsfeedType === 'CALENDAR') {
      this.handleOpenPopup({ popupName: 'appoint-customer', type: 'edit' })
    }
    if (post.newsfeedType === 'FILE') {
      this.handleOpenPopup({ popupName: 'add-upload-file', type: 'edit' })
    }
  }

  handleDelete(post: Record<string, any>): void {
    this.message = this.checkTypePost(post.newsfeedType)
    this.setOpenPopup({
      popupName: 'popup-confirm-type',
      isOpen: true
    })
    this.post = { ...post }
  }
  handleConfirmDelete(): void {
    this.apiCustomer.deletePost(this.post.id).then(() => {
      const text = this.checkTypePost(this.post.newsfeedType)
      this.$message.success(`Xa ${text} thnh cng`)
      this.resetQuery()
      this.init()
    })
  }

  handleOpenPopup(data: { popupName: string; type: string }): void {
    const { popupName, type } = data
    this.type = type
    console.log('sdsds', data)
    this.setOpenPopup({
      popupName,
      isOpen: true
    })
  }
}
