import { Post } from '@/modules/customer/interface'
import Reaction from './Reaction.vue'
import PostEmoji from './PostEmoji.vue'
import Comment from './Comment.vue'
import { forEach } from 'lodash'
import { namespace } from 'vuex-class'

const beCustomer = namespace('beCustomer')

@Component({ components: { Reaction, PostEmoji, Comment } })
export default class PostItem extends Vue {
  @Prop({ required: false, type: Boolean, default: true }) isPost!: boolean
  @Prop({ required: true, type: Object, default: {} }) post!: Record<string, any>

  @beCustomer.State('detailCustomer') detailCustomer!: Record<string, any>

  isShowComment = false
  isEnd = false
  focusId = 0
  isReset = false
  openPost = -1
  showFullText = []
