  isOpenPopver = false
  changeType = false
  topEmoji = []
  likeConflict = false
  isPlayVideo = false

  get postData(): Record<string, any> {
    return {
      post: this.post
    }
  }

  get getListImage(): string[] {
    const arrImage: string[] = []
    forEach(this.post.images, item => {
      arrImage.push(item.url)
    })
    return arrImage
  }

  get getTitleAction(): Record<string, any> {
    if (this.post.newsfeedType === 'NOTE') {
      return {
        edit: 'Sa ghi ch',
        delete: 'Xa ghi ch'
      }
    }
    if (this.post.newsfeedType === 'CALENDAR') {
      return {
        edit: 'Sa lch hn',
        delete: 'Xa lch hn'
      }
    }
    if (this.post.newsfeedType === 'DEAL') {
      return {
        edit: 'Sa c hi bn hng',
        delete: 'Xa c hi bn hng'
      }
    }
    return {
      edit: 'Sa file ti liu',
      delete: 'Xa file ti liu'
    }
  }

  get getTitle(): string {
    if (this.post.newsfeedType === 'NOTE') {
      return 'Ghi ch'
    }
    if (this.post.newsfeedType === 'CALENDAR') {
      return 'Hn lch t vn gi bo him'
    }
    if (this.post.newsfeedType === 'DEAL') {
      return 'C hi bn hng'
    }
    return 'File ti liu'
  }
  get getIcon(): string {
    if (this.post.newsfeedType === 'NOTE') {
      return 'icon-note'
    }
    if (this.post.newsfeedType === 'CALENDAR') {
      return 'icon-calendar'
    }
    if (this.post.newsfeedType === 'DEAL') {
      return 'icon-sale'
    }
    return 'icon-file'
  }

  clickFile(): void {
    this.handleViewPost()
  }
  handleViewPost(): void {
    if (!this.post.isView && !this.post.iViewedThis) {
      // this.viewPost(this.post.id)
      this.post.isView = true
      this.post.iViewedThis = true
      this.post.numOfView++
    }
  }

  handleViewMoreImage(): void {
    //@ts-ignore
    let el = this.$refs.image[1].$el
    let child = el.querySelector('.el-image__inner')
    child.click()
  }

  handleViewImage(post: Record<string, any>, active: number): void {
    this.handleViewPost()
