  downloadFile(url: string): void {
    window.open(`${url}`, '_blank')
  }

  handleEdit(post: Record<string, any>): void {
    this.$emit('edit', post)
  }
  handleDelete(post: Record<string, any>): void {
    this.$emit('delete', post)
  }
