































import { Component, Prop, Vue } from 'vue-property-decorator'
import { iconEmoji1, iconEmoji } from '@/configs/const/emoji'

@Component
export default class PostEmoji extends Vue {
  @Prop({ required: false, type: Number, default: 0 }) id!: number
  @Prop({ required: true, type: Boolean, default: false }) like!: boolean
  @Prop({ required: false, type: String, default: 'LIKE' }) type!: string
  @Prop({ required: false, type: String, default: 'post' }) position!: string
  icons = iconEmoji
  iconEmoji1 = iconEmoji1
  label = {
    LIKE: 'Thch',
    HAHA: 'Haha',
    ANGRY: 'Phn n',
    SAD: 'Bun',
    LOVE: 'Yu thch',
    WOW: 'Wow'
  }
  hover = false
  clearId = null
  colors = {
    LIKE: '#1962EF',
    HAHA: '#F7A738',
    ANGRY: '#E66B14',
    SAD: '#F7A738',
    LOVE: '#E2243B',
    WOW: '#F7A738'
  }

  handleLike(): void {
    if (this.like) {
      this.hover = false
      if (this.position === 'post') {
        this.$emit('like', 'UNLIKE')
      } else {
        this.$emit('like', {
          type: 'UNLIKE',
          id: this.id
        })
      }
    } else {
      this.hover = false
      if (this.position === 'post') {
        this.$emit('like', 'LIKE')
      } else {
        this.$emit('like', {
          type: 'LIKE',
          id: this.id
        })
      }
    }
  }
}
