  init(): void {
    if (this.post.comments !== undefined && this.post.comments !== null && this.comment_.id === undefined) {
      const list = this.post.comments.map((elm: any) => {
        const e: any = {}
        Object.assign(e, elm)
        e.isOpenChild = false
        e.isSubloading = false
        return e
      })
      this.tableIndex = this.post.comments.length
      //@ts-ignore
      this.tableList.unshift(...list.reverse())
    }
    if (this.comment_.id !== undefined) {
      console.log('init')
      this.tableIndex = 0
      this.$emit('subLoading', true)
      this.showComment()
        .then((res: any) => {
          if (res.data.data.length < 20) {
            this.$emit('endSubComment', true)
          }
          this.$emit('subLoading', false)
          //@ts-ignore
          this.$refs.divEditText.focus()
          this.$forceUpdate()
        })
        .catch(() => {
          this.$emit('subLoading', false)
        })
    }
  }
