  handleReduce(files: any) {
    // eslint-disable-next-line no-async-promise-executor
    return new Promise(async (resolve, reject) => {
      const filess: any[] = []
      await files.forEach(async (elm: any) => {
        if (elm.raw.type.startsWith('image')) {
          // const file = await reduceImage(elm, 3)
          // filess.push(file)
        } else {
          filess.push(elm)
        }
        if (filess.length === files.length) {
          resolve(filess)
        }
      })
    })
  }
