  getCaretPosition(editableDiv: any): any {
    let caretPos: any = 0
    let sel: any = 0
    let range: any = 0
    if (window.getSelection) {
      sel = window.getSelection()
      if (sel.rangeCount) {
        range = sel.getRangeAt(0)
        if (range.commonAncestorContainer.parentNode === editableDiv || range.commonAncestorContainer.parentNode.parentNode === editableDiv) {
          forEach(editableDiv.childNodes, value => {
            if (range.commonAncestorContainer === value) {
              caretPos += range.endOffset
              return false
            } else {
              caretPos += value.innerText ? value.innerText.length : value.textContent.length
            }
          })
        }
      }
    }
    //@ts-ignore
    else if (document.selection && document.selection.createRange) {
      //@ts-ignore
      range = document.selection.createRange()
      if (range.parentElement() === editableDiv) {
        const tempEl = document.createElement('span')
        editableDiv.insertBefore(tempEl, editableDiv.firstChild)
        const tempRange = range.duplicate()
        tempRange.moveToElementText(tempEl)
        tempRange.setEndPoint('EndToEnd', range)
        caretPos = tempRange.text.length
      }
    }
    return caretPos
  }
  convertHtmlToString(html: any): string {
    let result = ''
    const userInHtml: any = []
    forEach(html, value => {
      if (value.nodeName === '#text') {
        result += value.textContent
      } else if (value.nodeName === 'SPAN' || value.nodeName === 'span') {
        if (value.id && includes(value.id, 'mention-user-')) {
          const userId = replace(value.id, 'mention-user-', '')
          const userInfo = find(this.mentionsSelected, (value: any) => {
            return parseInt(userId) === parseInt(value.userId)
          })
          if (userInfo && startsWith(userInfo.name, value.innerText) && value.innerText !== '') {
            result += `@${userId}`
            userInHtml.push(parseInt(userId))
          } else {
            if (!value.innerText) {
              const parent = value.parentNode
              parent.removeChild(value)
            } else {
              value.classList.remove('mention-user')
              value.removeAttribute('id')
              result += value.innerText
            }
          }
        } else {
          result += value.innerText
        }
      } else {
        if (value.innerText.endsWith('\n')) {
          result += '\n' + value.innerText.substring(0, value.innerText.length - 1)
        } else {
          result += '\n' + value.innerText
        }
      }
    })
    //@ts-ignore
    this.mentionsSelected = filter(this.mentionsSelected, (value: any) => {
      return findIndex(userInHtml, elm_ => elm_ === parseInt(value.userId)) !== -1
    })
    return result
  }

