  // eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
  doSetCaretPosition(oField: any, iCaretPos: any): void {
    if (iCaretPos >= 0) {
      let selection: any = window.getSelection()
      const range = this.createRange(oField, { count: iCaretPos })
      if (range) {
        range.collapse(false)
        selection.removeAllRanges()
        selection.addRange(range)
      }
    }
  }
  // eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
  createRange(node: any, chars: any, range?: any): any {
    if (!range) {
      range = document.createRange()
      range.selectNode(node)
      range.setStart(node, 0)
    }
    if (chars.count === 0) {
      range.setEnd(node, chars.count)
    } else if (node && chars.count > 0) {
      if (node.nodeType === Node.TEXT_NODE) {
        if (node.textContent.length < chars.count) {
          chars.count -= node.textContent.length
        } else {
          range.setEnd(node, chars.count)
          chars.count = 0
        }
      } else {
        for (let lp = 0; lp < node.childNodes.length; lp++) {
          range = this.createRange(node.childNodes[lp], chars, range)
          if (chars.count === 0) {
            break
          }
        }
      }
    }
    return range
  }
  // eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
  setData(text: any, mentions: any, hashtags: any) {
    //@ts-ignore
    this.text = sanitizeHtml(text)
    this.mentionsSelected = mentions
    this.hashtagList = hashtags
    //@ts-ignore
    this.$refs.text.innerHTML = this.convertStringToHtml()
    this.focus()
  }
  convertStringToHtml(): string {
    let html = this.text
    forEach(this.hashtagList, (value: any) => {
      if (value.startsWith('#')) {
        const span = `<span class="hashtag-edit" contenteditable="true">${value}</span>`
        html = replace(html, new RegExp(`${value}`, 'g'), span)
      }
    })
    forEach(this.mentionsSelected, (value: any) => {
      const span = `<span id="mention-user-${value.userId}" class="mention-user" contenteditable="false">${value.name}</span>`
      html = replace(html, new RegExp(`@${value.userId}`, 'g'), span)
    })
    return html
  }
