


















import { Component, Mixins, Prop } from 'vue-property-decorator'
import PopupMixin from '@/mixins/popup'
import DistrictMixin from '@/mixins/commonMixin'

@Component({ components: {} })
export default class PopupDeleteAll extends Mixins(PopupMixin, DistrictMixin) {
  @Prop() isDelete!: string

  handleCancel(): void {
    this.setOpenPopup({
      popupName: 'popup-delete',
      isOpen: false
    })
  }
  handleConfirm(): void {
    this.handleCancel()
    this.$emit('confirm')
  }
  handleOpen(): void {
    console.log(this.isDelete)
  }
  handleClose(): void {
    console.log('dng')
  }
}
