


















import { Component, Mixins, Prop } from 'vue-property-decorator'
import PopupMixin from '@/mixins/popup'
import DistrictMixin from '@/mixins/commonMixin'
@Component({ components: {} })
export default class PopupConfirmType extends Mixins(PopupMixin, DistrictMixin) {
  @Prop()
  message!: string

  handleCancel(): void {
    this.setOpenPopup({
      popupName: 'popup-confirm-type',
      isOpen: false
    })
  }
  handleConfirm(): void {
    this.setOpenPopup({
      popupName: 'popup-confirm-type',
      isOpen: false
    })
    this.$emit('confirm')
  }
}
