









import { Component, Prop, Vue } from 'vue-property-decorator'
import { iconEmoji1 } from '@/configs/const/emoji'
import { split } from 'lodash'

@Component
export default class Reaction extends Vue {
  @Prop({ required: false, type: Boolean, default: false }) isShort!: boolean
  @Prop({ required: true, type: Object, default: {} }) post!: Record<string, any>

  iconEmoji = iconEmoji1

  get topEmoji(): string[] {
    return split(this.post.topEmoji, ',')
  }
  handleShow(): string {
    return ''
  }
}
